'use client';

interface HourglassProgressProps {
  progress: number; // 0-100
  isActive: boolean;
}

export default function HourglassProgress({ progress, isActive }: HourglassProgressProps) {
  const upperFill = progress > 50 ? 0 : 100 - (progress * 2);
  const lowerFill = progress <= 50 ? 0 : (progress - 50) * 2;

  if (!isActive) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-white/80 backdrop-blur-md">
      <div className="text-center">
        <div className="relative w-32 h-40 mx-auto mb-6">
          {/* Hourglass Container */}
          <svg
            viewBox="0 0 120 160"
            className="w-full h-full drop-shadow-2xl"
          >
            {/* Upper Section */}
            <path
              d="M 20 20 L 60 60 L 100 20 Z"
              fill="none"
              stroke="#9333ea"
              strokeWidth="3"
            />
            
            {/* Upper Sand */}
            {progress <= 50 && (
              <rect
                x="20"
                y={20 + (upperFill * 0.4)}
                width="80"
                height={40 - (upperFill * 0.4)}
                fill="#c084fc"
                opacity="0.8"
              />
            )}

            {/* Middle Connector */}
            <rect x="55" y="60" width="10" height="8" fill="#9333ea" />

            {/* Lower Section */}
            <path
              d="M 20 68 L 60 28 L 100 68 L 100 140 L 20 140 Z"
              fill="none"
              stroke="#9333ea"
              strokeWidth="3"
            />

            {/* Lower Sand */}
            {progress > 50 && (
              <rect
                x="20"
                y={140 - (lowerFill * 0.72)}
                width="80"
                height={lowerFill * 0.72}
                fill="#c084fc"
                opacity="0.8"
              />
            )}

            {/* Falling Sand */}
            {progress > 45 && progress < 55 && (
              <circle
                cx="60"
                cy={60 + ((progress - 45) * 4)}
                r="3"
                fill="#c084fc"
              />
            )}
          </svg>
        </div>
        
        <div className="space-y-2">
          <p className="text-2xl font-bold text-purple-600 animate-pulse">
            ANALYZING SAFETY...
          </p>
          <p className="text-lg text-purple-500 font-semibold">
            {Math.round(progress)}%
          </p>
          <div className="w-64 h-2 bg-purple-100 rounded-full overflow-hidden mx-auto">
            <div
              className="h-full bg-gradient-to-r from-purple-500 to-purple-600 transition-all duration-300 ease-out rounded-full"
              style={{ width: `${progress}%` }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

